.travels {
  padding: 120px 71px 145px;

  @include vp-1023 {
    padding: 100px 26px 95px 21px;
  }

  @include vp-767 {
    padding: 58px 0 85px;
  }
}

.travels-wrapper {
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    flex-direction: column;
  }
}

.travels-wrapper img {

  @include vp-1023 {
    width: 324px;
    height: 174px;
  }

  @include vp-767 {
    width: 290px;
    height: 156px;
  }
}

.travels-description {
  margin-top: 56px;
  margin-right: 82px;
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    margin-top: -10px;
    margin-right: 22px;
  }

  @include vp-767 {
    margin-top: 0;
    margin-right: 0;
  }
}

.travels-wrapper h3 {
  margin: 0;
  padding: 0;
  font-family: $ff-main-font;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: $color-tangaroa;
  margin-bottom: 25px;

  @include vp-1023 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 15px;
    max-width: 210px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 34px;
  }
}

.travels-wrapper p {
  margin: 0;
  padding: 0;
  font-family: $ff-main-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  @include vp-767 {
    margin-bottom: 42px;
    min-width: 280px;
  }
}

