.booking-form{
  background-image: url('../../img/bg-form-desktop.jpg');
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}

.form-wrapper {
  position: relative;
  padding: 50px 129px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    flex-direction: column;
    padding: 94px 149px 100px 151px;
  }

  @include vp-767 {
    flex-direction: column;
    padding: 65px 0 100px;
  }
}

.form-description {
  margin-right: 144px;
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    margin-right: 0;
    margin-bottom: 50px;
  }

  @include vp-767 {
    margin-bottom: 42px;
    align-items: center;
  }
}

.form-description h2 {
  margin: 0;
  padding: 0;
  font-family: $ff-main-font;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  color: $color-tangaroa;
  margin-top: -5px;
  margin-bottom: 5px;

  @include vp-1023 {
    font-size: 34px;
    line-height: 51px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 31px;
    text-align: center;
    margin-bottom: 13px;
  }
}

.form-description p {
  margin: 0;
  padding: 0;
  font-family: $ff-main-font;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: $color-tangaroa;

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    margin-bottom: -10px;
  }
}

.form-block {
  background-color: $color-tangaroa;
  padding: 43px 50px 50px;
  max-width: 404px;

  @include vp-1023 {
    padding-top: 45px;
    padding-bottom: 52px;
  }

  @include vp-767 {
    padding: 35px 28px 50px;
  }
}

.form-block p {
  margin: 0;
  padding: 0;
  font-family: $ff-main-font;
  color: $color-alice-blue;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 27px;

  @include vp-767 {
    line-height: 25px;
    margin-bottom: 29px;
    text-align: center;
    margin: 0 20px 29px 20px;
  }
}

.form__name,
.form__phone,
.form__mail {
  margin-bottom: 20px;
}

.form__name label,
.form__phone label,
.form__mail label {
  display: none;
}

.form__name input,
.form__phone input,
.form__mail input {
  height: 43px;
  border: none;
  width: 100%;
  padding-left: 14px;

  @include vp-767 {
    padding-left: 16px;
  }
}

.form__name input:hover,
.form__phone input:hover,
.form__mail input:hover,
.form__name input:focus,
.form__phone input:focus,
.form__mail input:focus {
  background-color: $color-quartz;
}

.form__name input::-webkit-input-placeholder,
.form__phone input::-webkit-input-placeholder,
.form__mail input::-webkit-input-placeholder {
  color: rgba(1, 28, 64, 0.7);
}

.form__button input {
  height: 40px;
}

.form__checkbox {
  margin-bottom: 36px;
}

.form__checkbox input[type="checkbox"] {
  appearance: none;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form__checkbox label {
  font-family: $ff-main-font;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: $color-alice-blue;
  cursor: pointer;
  position: relative;

  @include vp-767 {
    line-height: 12px;
  }
}

.form__checkbox span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 16px;
  height: 16px;
  background-color: $color-alice-blue;
  border: 1px solid $color-alice-blue;

  @include vp-1023 {
  }
}

.form__checkbox span {
  position: relative;
  display: block;
  padding-left: 22px;
}

.form__checkbox:hover span::before {
  background-color: $color-quartz;
  border: 1px solid $color-quartz;
}

.form__checkbox label input[type="checkbox"]:focus + span::before {
  background-color: $color-quartz;
  border: 1px solid $color-quartz;
}

.form__checkbox span::after {
  content: " ";
  position: absolute;
  top: 0;
  left: 3px;
  transform: translateY(80%);
  display: none;
  width: 8px;
  height: 6px;
  background-image: url("../img/svg/icon-checked.svg");
  background-repeat: no-repeat;
}

.form__checkbox label input[type="checkbox"]:checked + span::after {
  display: block;
}
